'use client';

import { useState } from 'react';
// // import { "üê¶" } from 'react-icons/fa'; // ‰∏¥Êó∂Ê≥®Èáä‰ª•‰øÆÂ§çÊûÑÂª∫ // ‰∏¥Êó∂Ê≥®Èáä‰ª•‰øÆÂ§çÊûÑÂª∫

const TopicBoxForm = () => {
  const [postUrl, setPostUrl] = useState<string>('');
  const [hashtag, setHashtag] = useState<string>('');
  const [boxCount, setBoxCount] = useState<number>(10);
  const [rewardPerBox, setRewardPerBox] = useState<number>(50);
  const [totalCost, setTotalCost] = useState<number>(500);
  const [fee, setFee] = useState<string>('0.1 - 5 SM');
  const [error, setError] = useState<string>('');

  // ÈìæÊé•ÂèòÊõ¥
  const handleUrlChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPostUrl(e.target.value);
  };

  // ËØùÈ¢òÊ†áÁ≠æÂèòÊõ¥
  const handleHashtagChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    // Ëá™Âä®Ê∑ªÂä†#Âè∑ÂâçÁºÄ
    if (value && !value.startsWith('#')) {
      setHashtag(`#${value}`);
    } else {
      setHashtag(value);
    }
  };

  // ÂÆùÁÆ±Êï∞ÈáèÂèòÊõ¥
  const handleBoxCountChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = parseInt(e.target.value) || 0;
    setBoxCount(value);
    calculateTotalCost(value, rewardPerBox);
  };

  // Â•ñÂä±ÂèòÊõ¥
  const handleRewardChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = parseInt(e.target.value) || 0;
    setRewardPerBox(value < 50 ? 50 : value); // ÊúÄ‰Ωé50Â∞èÁ∫¢Ëä±
    calculateTotalCost(boxCount, value < 50 ? 50 : value);
  };

  // ËÆ°ÁÆóÊÄªÊàêÊú¨
  const calculateTotalCost = (boxes: number, reward: number) => {
    const total = boxes * reward;
    setTotalCost(total);

    // ÂÅáËÆæÁöÑÊâãÁª≠Ë¥πËÆ°ÁÆó
    if (total > 0) {
      if (total < 1000) {
        setFee('0.1 - 5 SM');
      } else if (total < 5000) {
        setFee('5 - 20 SM');
      } else {
        setFee('20+ SM');
      }
    } else {
      setFee('0 SM');
    }
  };

  // Êèê‰∫§Ë°®Âçï
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    // ÁÆÄÂçïÈ™åËØÅ
    if (!postUrl) {
      setError('ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑXÂ∏ñÂ≠ê/ËØùÈ¢òÈìæÊé•');
      return;
    }

    if (!hashtag) {
      setError('ËØ∑ËæìÂÖ•ËØùÈ¢òÊ†áÁ≠æ');
      return;
    }

    if (boxCount <= 0) {
      setError('ÂÆùÁÆ±Êï∞ÈáèÂøÖÈ°ªÂ§ß‰∫é0');
      return;
    }

    if (rewardPerBox < 50) {
      setError('ÊØè‰∏™ÂÆùÁÆ±Â•ñÂä±Ëá≥Â∞ë50Â∞èÁ∫¢Ëä±');
      return;
    }

    // Ê®°Êãü‰ΩôÈ¢ùÊ£ÄÊü•
    const mockBalance = 5000; // ÂÅáËÆæÁöÑÂ∞èÁ∫¢Ëä±‰ΩôÈ¢ù
    if (totalCost > mockBalance) {
      setError('Â∞èÁ∫¢Ëä±‰ΩôÈ¢ù‰∏çË∂≥ÔºåËØ∑ÂâçÂæÄÂ∏ÇÂú∫');
      return;
    }

    setError('');
    console.log('ÂÆùÁÆ±‰ªªÂä°ÂàõÂª∫Ôºö', {
      postUrl,
      hashtag,
      boxCount,
      rewardPerBox,
      totalCost,
    });
    // ËøôÈáåÂèØ‰ª•Ê∑ªÂä†ÂÆûÈôÖÁöÑÊèê‰∫§ÈÄªËæë
  };

  return (
    <div className="tech-card">
      <h2 className="text-2xl font-bold mb-6">ÂàõÂª∫ X ËØùÈ¢òÂÆùÁÆ±</h2>

      <form onSubmit={handleSubmit}>
        {/* Âπ≥Âè∞ÊòæÁ§∫ */}
        <div className="mb-6 flex items-center">
          üê¶
          <span className="text-lg">Âπ≥Âè∞: X (Twitter)</span>
        </div>

        {/* XÂ∏ñÂ≠ê/ËØùÈ¢òÈìæÊé• */}
        <div className="mb-6">
          <label
            htmlFor="post-url"
            className="block text-sm font-medium text-gray-300 mb-2"
          >
            X Â∏ñÂ≠ê/ËØùÈ¢òÈìæÊé•
          </label>
          <input
            id="post-url"
            type="text"
            placeholder="https://twitter.com/..."
            value={postUrl}
            onChange={handleUrlChange}
            className="input-field w-full"
          />
        </div>

        {/* ËØùÈ¢òÊ†áÁ≠æ */}
        <div className="mb-6">
          <label
            htmlFor="hashtag"
            className="block text-sm font-medium text-gray-300 mb-2"
          >
            ËØùÈ¢òÊ†áÁ≠æ (#)
          </label>
          <input
            id="hashtag"
            type="text"
            placeholder="#SocioMint"
            value={hashtag}
            onChange={handleHashtagChange}
            className="input-field w-full"
          />
        </div>

        {/* ÂÆùÁÆ±Êï∞Èáè */}
        <div className="mb-6">
          <label
            htmlFor="box-count"
            className="block text-sm font-medium text-gray-300 mb-2"
          >
            ÂÆùÁÆ±Êï∞Èáè
          </label>
          <input
            id="box-count"
            type="number"
            min="1"
            value={boxCount}
            onChange={handleBoxCountChange}
            className="input-field w-full"
          />
        </div>

        {/* ÊØè‰∏™ÂÆùÁÆ±Â•ñÂä± */}
        <div className="mb-6">
          <label
            htmlFor="reward-per-box"
            className="block text-sm font-medium text-gray-300 mb-2"
          >
            ÊØè‰∏™ÂÆùÁÆ±Â•ñÂä±Â∞èÁ∫¢Ëä± (ÊúÄ‰Ωé 50)
          </label>
          <input
            id="reward-per-box"
            type="number"
            min="50"
            value={rewardPerBox}
            onChange={handleRewardChange}
            className="input-field w-full"
          />
        </div>

        {/* ÊàêÊú¨ËÆ°ÁÆó */}
        <div className="mb-6 p-4 bg-gray-800/50 rounded-md border border-gray-700">
          <div className="flex justify-between items-center mb-2">
            <span className="text-gray-300">ÊÄªÂ∞èÁ∫¢Ëä±ÊàêÊú¨:</span>
            <span className="text-xl font-bold text-primary">{totalCost}</span>
          </div>
          <div className="flex justify-between items-center">
            <span className="text-gray-300">È¢ÑËÆ°ÊâãÁª≠Ë¥π:</span>
            <span className="text-secondary">{fee}</span>
          </div>
        </div>

        {/* ÈîôËØØÊèêÁ§∫ */}
        {error && (
          <div className="mb-6 p-3 bg-red-900/20 border border-red-500/30 rounded-md text-red-300">
            {error}
          </div>
        )}

        {/* Êèê‰∫§ÊåâÈíÆ */}
        <button type="submit" className="neon-button w-full">
          ÂàõÂª∫ÂÆùÁÆ±‰ªªÂä°
        </button>
      </form>
    </div>
  );
};

export default TopicBoxForm;
